"use client"
import { useEffect, useRef, useState } from "react"
import { galleryData } from "../data/gallery"

export default function CircularGallery({ bend = 3, textColor = "#ffffff", borderRadius = 0.05, scrollEase = 0.02 }) {
  const containerRef = useRef(null)
  const [scrollPos, setScrollPos] = useState(0)
  const [selectedImage, setSelectedImage] = useState(null)

  useEffect(() => {
    const container = containerRef.current
    if (!container) return

    const handleScroll = (e) => {
      setScrollPos(e.target.scrollLeft)
    }

    container.addEventListener("scroll", handleScroll)
    return () => container.removeEventListener("scroll", handleScroll)
  }, [scrollEase])

  const calculateRotation = (index, totalItems) => {
    return (index / totalItems) * 360
  }

  const calculatePosition = (index, totalItems, radius = 150) => {
    const angle = calculateRotation(index, totalItems) * (Math.PI / 180)
    const x = Math.cos(angle) * radius
    const y = Math.sin(angle) * radius
    return { x, y }
  }

  return (
    <div
      ref={containerRef}
      className="w-full h-full relative overflow-hidden"
      style={{
        borderRadius: `${borderRadius * 100}%`,
        background: "radial-gradient(circle, rgba(74,222,128,0.1) 0%, rgba(139,92,246,0.05) 100%)",
      }}
    >
      {/* Center Point */}
      <div className="absolute top-1/2 left-1/2 w-4 h-4 bg-accent rounded-full transform -translate-x-1/2 -translate-y-1/2 z-50 shadow-lg"></div>

      {/* Circular Images Container */}
      <div className="absolute inset-0 flex items-center justify-center">
        <div className="relative w-80 h-80">
          {galleryData.map((image, idx) => {
            const { x, y } = calculatePosition(idx, galleryData.length, 130)
            const rotation = calculateRotation(idx, galleryData.length)

            return (
              <button
                key={image.id}
                onClick={() => setSelectedImage(image)}
                className="absolute group flex-shrink-0 transition-all duration-300 hover:scale-110"
                style={{
                  transform: `translate(calc(-50% + ${x}px), calc(-50% + ${y}px)) rotate(${rotation}deg)`,
                  left: "50%",
                  top: "50%",
                  width: "80px",
                  height: "80px",
                }}
              >
                <div className="w-full h-full bg-gray-200 rounded-lg shadow-lg overflow-hidden border-3 border-white hover:border-accent transition-colors">
                  <img
                    src={image.url || "/placeholder.svg"}
                    alt={image.alt}
                    className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-300"
                    onError={(e) => {
                      e.target.src = `/placeholder.svg?height=80&width=80&query=${image.alt}`
                    }}
                  />
                </div>
                <div
                  className="absolute inset-0 bg-black/0 group-hover:bg-black/30 transition-colors rounded-lg flex items-center justify-center opacity-0 group-hover:opacity-100"
                  style={{
                    transform: `rotate(-${rotation}deg)`,
                  }}
                >
                  <span className="text-white font-bold text-xs">{image.alt}</span>
                </div>
              </button>
            )
          })}
        </div>
      </div>

      {/* Title in Center */}
      <div className="absolute inset-0 flex flex-col items-center justify-center pointer-events-none">
        <p className="text-2xl font-bold" style={{ color: textColor }}>
          Gallery
        </p>
        <p className="text-sm opacity-70" style={{ color: textColor }}>
          Scroll to explore
        </p>
      </div>

      {/* Lightbox Modal */}
      {selectedImage && (
        <div className="fixed inset-0 bg-black/80 flex items-center justify-center p-4 z-50">
          <div className="relative max-w-2xl w-full">
            <button
              onClick={() => setSelectedImage(null)}
              className="absolute -top-12 right-0 text-white hover:text-accent transition-colors text-3xl"
            >
              ✕
            </button>
            <img
              src={selectedImage.url || "/placeholder.svg"}
              alt={selectedImage.alt}
              className="w-full h-auto rounded-lg shadow-2xl"
              onError={(e) => {
                e.target.src = `/placeholder.svg?height=400&width=600&query=${selectedImage.alt}`
              }}
            />
            <p className="text-white text-center mt-4 text-lg font-semibold">{selectedImage.alt}</p>
          </div>
        </div>
      )}
    </div>
  )
}