"use client"

import { useEffect, useState } from "react"

export default function Hero() {
  const [countdown, setCountdown] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  })

  useEffect(() => {
    const updateCountdown = () => {
      const targetDate = new Date("December 20, 2025 09:00").getTime()
      const now = new Date().getTime()
      const distance = targetDate - now

      if (distance > 0) {
        setCountdown({
          days: Math.floor(distance / (1000 * 60 * 60 * 24)),
          hours: Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
          minutes: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
          seconds: Math.floor((distance % (1000 * 60)) / 1000),
        })
      }
    }

    updateCountdown()
    const interval = setInterval(updateCountdown, 1000)
    return () => clearInterval(interval)
  }, [])

  const scrollToEvents = () => {
    document.getElementById("events")?.scrollIntoView({ behavior: "smooth" })
  }

  return (
    <section className="relative min-h-screen w-full flex items-center justify-center px-4 overflow-hidden">
      {/* Background video */}
      <video
        autoPlay
        loop
        muted
        playsInline
        className="absolute inset-0 w-full h-full object-cover z-0"
        onError={() => console.log("[v0] Video failed to load - check file path and format")}
      >
        <source src="/carnival-background.webm" type="video/webm" />
        <source src="/carnival-background.mp4" type="video/mp4" />
        Your browser does not support the video tag.
      </video>

      {/* Dark overlay above video */}
      <div className="absolute inset-0 bg-black/30 z-10" />

      {/* Content */}
      <div className="relative z-20 flex flex-col items-center text-center max-w-3xl">
        {/* Logo image – responsive sizes */}
        <img
          src="/Insight-name.webp"
          alt="INSIGHT Event Logo"
          className="w-56 sm:w-72 md:w-120 h-auto mb-2 mx-auto drop-shadow-[0_0_30px_rgba(255,255,255,0.6)]"
        />

        {/* College name – close to logo, no overlap */}
        <p className="mt-1 sm:mt-2 mb-6 px-4 text-sm sm:text-lg md:text-xl font-serif font-semibold text-[#F8D48A] drop-shadow-[0_0_10px_rgba(255,215,130,0.6)]">
          St. Vincent Pallotti College of Engineering and Technology, Nagpur
        </p>

        {/* Countdown Timer */}
        <div className="grid grid-cols-4 gap-2 sm:gap-4 mb-8 sm:mb-12 justify-center w-full">
          {[
            { value: countdown.days, label: "Days" },
            { value: countdown.hours, label: "Hours" },
            { value: countdown.minutes, label: "Minutes" },
            { value: countdown.seconds, label: "Seconds" },
          ].map((item, idx) => (
            <div key={idx} className="relative group">
              <div className="absolute inset-0 bg-gradient-to-br from-yellow-400 via-amber-300 to-orange-400 rounded-xl blur-lg opacity-60 group-hover:opacity-100 transition-opacity duration-300 animate-pulse-glow" />

              <div className="relative bg-gradient-to-br from-purple-500/30 via-pink-500/20 to-red-500/30 backdrop-blur-xl border border-white/20 rounded-xl p-3 sm:p-6 min-w-16 sm:min-w-24 transform group-hover:scale-110 transition-all duration-300 shadow-2xl">
                <div className="absolute inset-0 bg-gradient-to-t from-transparent via-white/10 to-white/20 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
                <div className="relative z-10">
                  <div className="text-lg sm:text-2xl md:text-4xl font-bold bg-gradient-to-r from-yellow-300 via-amber-200 to-yellow-200 bg-clip-text text-transparent">
                    {String(item.value).padStart(2, "0")}
                  </div>
                  <div className="text-[0.6rem] sm:text-xs md:text-sm font-semibold text-white/90 mt-1 drop-shadow-md">
                    {item.label}
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
