"use client"

import { useState } from "react"
import { Menu, X } from "lucide-react"

export default function Navbar({ activeSection, setActiveSection }) {
  const [isOpen, setIsOpen] = useState(false)

  const navItems = [
    { label: "Home", id: "home" },
    { label: "Cultural Points", id: "points" },
    { label: "Events", id: "events" },
    { label: "Gallery", id: "gallery" },
    { label: "Team", id: "team" },
  ]

  const handleNavClick = (id) => {
    setActiveSection(id)
    setIsOpen(false)
  }

  return (
    <nav className="fixed top-0 left-0 right-0 z-50 navbar-glass border-b border-white/10">
  <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
    <div className="flex justify-between items-center h-16 sm:h-20">

      <div className="flex items-center gap-3 text-white font-bold carnival-font drop-shadow-lg">
        <img
          src="/main-logo.webp"
          alt="INSIGHT Logo"
          className="h-14 sm:h-16 w-auto drop-shadow-lg hover:scale-105 transition-transform"
        />
        <span className="text-lg sm:text-2xl font-sans">INSIGHT 25–26</span>
      </div>



          {/* Desktop Navigation */}
          <div className="hidden md:flex gap-8">
            {navItems.map((item) => (
              <button
                key={item.id}
                onClick={() => handleNavClick(item.id)}
                className={`transition-all duration-300 font-semibold text-sm drop-shadow-md ${
                  activeSection === item.id ? "text-accent scale-110" : "text-white hover:text-accent"
                }`}
              >
                {item.label}
              </button>
            ))}
          </div>

          {/* Mobile Menu Button */}
          <button className="md:hidden text-white p-2 drop-shadow-lg" onClick={() => setIsOpen(!isOpen)}>
            {isOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Mobile Navigation */}
        {isOpen && (
          <div className="md:hidden pb-4 space-y-2 border-t border-white/10 navbar-glass-mobile">
            {navItems.map((item) => (
              <button
                key={item.id}
                onClick={() => handleNavClick(item.id)}
                className={`block w-full text-left px-4 py-2 rounded transition-all ${
                  activeSection === item.id ? "bg-accent text-primary font-bold" : "text-white hover:bg-white/10"
                }`}
              >
                {item.label}
              </button>
            ))}
          </div>
        )}
      </div>
    </nav>
  )
}
